import "./styles.css";

const onClickAdd = () => {
  // ãƒ†ã‚­ã‚¹ãƒˆãƒœãƒƒã‚¯ã‚¹ã®å€¤ã‚’å–å¾—ã—åˆæœŸåŒ–ã™ã‚‹
  const inputText = document.getElementById("add-text").value;
  document.getElementById("add-text").value = "";

  createIncompleteList(inputText);
};

// æœªå®Œäº†ãƒªã‚¹ãƒˆã‹ã‚‰æŒ‡å®šã®è¦ç´ ã‚’då‰Šé™¤
const deleteFromIncompleteList = (target) => {
  document.getElementById("incomplete-list").removeChild(target);
};

// æœªå®Œäº†ãƒªã‚¹ãƒˆã«è¿½åŠ ã™ã‚‹é–¢æ•°
const createIncompleteList = (text) => {
  // liç”Ÿæˆ
  const li = document.createElement("li");
  li.className = "list-low";

  // pç”Ÿæˆ
  const p = document.createElement("p");
  p.className = "list-low-text";
  p.innerText = text;

  // liã‚¿ãƒ–ã®å°è¦ç´ ã«å„è¦ç´ ã‚’è¨­å®š
  li.appendChild(p);

  // å®Œäº†buttonç”Ÿæˆ
  const completeButton = document.createElement("button");
  completeButton.innerText = "å®Œäº†";
  completeButton.addEventListener("click", () => {
    // æŠ¼ã•ã‚ŒãŸå®Œäº†ãƒœã‚¿ãƒ³ã®è¦ªã‚¿ã‚°ã€€(li)ã‚’æœªå®Œäº†ãƒªã‚¹ãƒˆã‹ã‚‰å‰Šé™¤
    deleteFromIncompleteList(completeButton.parentNode);

    // å®Œäº†ãƒªã‚¹ãƒˆã«è¿½åŠ ã™ã‚‹è¦ç´ 
    const addTarget = completeButton.parentNode;

    // TODOå†…å®¹ãƒ†ã‚­ã‚¹ãƒˆã‚’å–å¾—
    const text = addTarget.firstElementChild.innerText;

    // liä»¥ä¸‹ã‚’åˆæœŸåŒ–
    addTarget.textContent = null;

    // pã‚¿ã‚°ç”Ÿæˆ
    const p = document.createElement("p");
    p.innerText = text;

    // buttonã‚¿ã‚°ç”Ÿæˆ
    const backButton = document.createElement("button");
    backButton.innerText = "æˆ»ã™";
    backButton.addEventListener("click", () => {
      // æŠ¼ã•ã‚ŒãŸæˆ»ã™ãƒœã‚¿ãƒ³ã®è¦ªã‚¿ã‚°(li)ã‚’å®Œäº†ãƒªã‚¹ãƒˆã‹ã‚‰å‰Šé™¤
      const deleteTarget = backButton.parentNode;
      document.getElementById("complete-list").removeChild(deleteTarget);

      // ãƒ†ã‚­ã‚¹ãƒˆå–å¾—
      const text = backButton.parentNode.firstChild.innerText;
      createIncompleteList(text);
    });

    // pã‚¿ã‚°ã®å°è¦ç´ ã«å„è¦ç´ ã‚’è¨­å®š
    addTarget.appendChild(p);
    addTarget.appendChild(backButton);

    // å®Œäº†ãƒªã‚¹ãƒˆã«è¿½åŠ 
    document.getElementById("complete-list").appendChild(addTarget);
  });

  // å‰Šé™¤buttonç”Ÿæˆ
  const deleteButton = document.createElement("button");
  deleteButton.innerText = "å‰Šé™¤";
  deleteButton.addEventListener("click", () => {
    // æŠ¼ã•ã‚ŒãŸå‰Šé™¤ãƒœã‚¿ãƒ³ã®è¦ªã‚¿ã‚°(li)ã‚’æœªå®Œäº†ãƒªã‚¹ãƒˆã‹ã‚‰å‰Šé™¤
    deleteFromIncompleteList(deleteButton.parentNode);
  });

  // liã‚¿ã‚°ã®å­è¦ç´ ã«å„è¦ç´ ã‚’è¨­å®š
  li.appendChild(p);
  li.appendChild(completeButton);
  li.appendChild(deleteButton);

  // æœªå®Œäº†ãƒªã‚¹ãƒˆã«è¿½åŠ 
  document.getElementById("incomplete-list").appendChild(li);
};

document
